import React       from 'react'
import { connect } from 'react-redux'
import {
  getLayerRange,
} from '../../../store/selectors'
import styles from './styles.module.scss'
import Layer  from '../Layer'
import {
  applySpec,
  map,
} from 'ramda'

export default connect(
  applySpec({
    range: getLayerRange,
  })
)(
  ({ range }) => (
    <guitar className={ styles.guitar }>
      { map(i => (
        <Layer
          key  ={ i }
          layer={ i }
        />
      ))(range) }
    </guitar>
  )
)
